



































































import md5 from "@/common/SDK/md5.min.js";
import general from "@/common/utils/general.js"
export default {
	data() {
		return {
			phoneNumber: '13983171812', 
			passwd:'',
			code:'', //输入的图形验证码
			rCode:'', //生成的图形验证码
			isShowOauth:true,
			showProvider:{
				weixin:false,
				qq:false,
				sinaweibo:false,
				xiaomi:false
			}
		}
	},
	onShow() {
		
	},
	onLoad() {
		//APP显示第三方登录



		this.getProvider();
		// console.log()
		this.rCode = this.randomCode()
	}, 
	
	methods: {
		resetpasswd(){
			uni.navigateTo({
				url:'resetpasswd'
			})
		},
		randomCode(){ //生成图片验证码
			// 大写字母code码 65~90
			let n1 = Math.floor(Math.random()*25)+65;
			let n2 = Math.floor(Math.random()*25)+65;
			let n3 = Math.floor(Math.random()*25)+65;
			let n4 = Math.floor(Math.random()*25)+65;
			var codeStr= this.cToL(n1)+this.cToL(n2)+this.cToL(n3)+this.cToL(n4)
			console.log(n1,n2,n3,n4)
			return codeStr;
		},
		cToL(n){
			return String.fromCharCode(n)+''
		},
		changeCode(){ //点击切换图片验证码
			this.rCode = this.randomCode()
		},
		reg(){
			uni.navigateTo({
				url:'register'
			})
		},
		oauthLogin(provider){
			uni.showLoading();
			//第三方登录
			uni.login({
				provider: provider,
				success: (loginRes)=>{
					console.log("success: "+JSON.stringify(loginRes));
					//案例直接获取用户信息，一般不是在APP端直接获取用户信息，比如微信，获取一个code，传递给后端，后端再去请求微信服务器获取用户信息
					uni.getUserInfo({
						provider: provider,
						success: (infoRes)=>{
							console.log('用户信息：' + JSON.stringify(infoRes.userInfo));
							uni.setStorage({
								key: 'UserInfo',
								data: {
									username:infoRes.userInfo.nickName,
									face:infoRes.userInfo.avatarUrl,
									signature:'个性签名',
									integral:0,
									balance:0,
									envelope:0
								},
								success: function () {
									uni.hideLoading()
									uni.showToast({title: '登录成功',icon:"success"});
									setTimeout(function(){
										uni.navigateBack();
									},300)
								}
							});
						}
					});
				},
				fail:(e)=>{
					console.log("fail: "+JSON.stringify(e));
				}
			});
		},
		getProvider(){
			//获取第三方登录服务
			uni.getProvider({
				service: 'oauth',
				success: (res)=>{
					let len = res.provider.length;
					for(let i=0;i<len;i++){
						//有服务才显示按钮图标
						this.showProvider[res.provider[i]] = true;
					}
					if(res.provider.length==0){
						this.isShowOauth=false;
					}
				}
			});
		},
		toPage(page){
			uni.hideKeyboard()
			uni.navigateTo({
				url: page
			});
		},
		async doLogin(){ //登录按钮逻辑
			uni.hideKeyboard();
			//验证手机号码
			if(!(/^1(3|4|5|6|7|8|9)\d{9}$/.test(this.phoneNumber))){ 
				uni.showToast({title: '请填写正确手机号码',icon:"none"});
				return false; 
			}
			if(this.code.toLowerCase()!=this.rCode.toLowerCase()){
				uni.showToast({title: '验证码错误',icon:"none"});
				return false; 
			}
			if(this.passwd==''){
				uni.showToast({title: '密码不能为空',icon:"none"});
				return false; 
			}
			uni.showLoading({
				title: '登录中...'
			})
			let loginData = await general.generalGET(
				this.global.baseUrl+'doLogin',
				{
					phone:this.phoneNumber,
					pwd:this.passwd
				}
			)
			 console.log(loginData)
			if(loginData.data.code=='OK'){
				loginData.data.userinfo.token = loginData.data.token;
				let userinfo = JSON.stringify(loginData.data.userinfo)
				console.log(userinfo)
				uni.setStorage({
					key:'userinfo',
					data:userinfo
				})
				uni.navigateTo({
					url:'../home/home'
				})
			}
			if(loginData.data.code=='pwdErr'){
				uni.showToast({title: '密码错误',icon:"none"});
			}
			 uni.hideLoading()
		}
	}
}
